.Footer2 {
    background-color: black;
    height: 300px;
    line-height: 40px;
  }
  .Footer2-div {
   
    /* padding-right: 50px; */
    
    
    display: flex;
    flex-flow: row wrap;
    justify-content: space-around;
    align-items: center;
  }
  .Footer2 ul {
    list-style: none;
  }
  .Footer2 a {
    text-decoration: none;
    color: white;
    font-size: 15px;
  }
  .Footer2 h6 {
    font-size: 20px;
    margin-bottom: 20px;
    padding-left: 120px;
    color: grey;
  }
  .Footer2 hr {
    color: white;
    background-color: white;
    margin: 0px 120px;
    height: 1px;
  }