


/* header */

@media screen and (max-width: 690px){
  .main{
      height:640px !important;
      padding-right: 15px!important;
  }
  .main-right{
      z-index: -1;
      position: absolute!important;
      opacity: 0.3;
      height: 640px !important;
  }
  .input-location::placeholder{
      color: transparent !important;
  }
  .input .input-location .locate-button
  {
    display: none;
  }
  .logo-main{
      width: 130px !important;
      height: 30px !important;
  }
}

.main{
  display: flex;
  justify-content: right;
  height: 540px;
  overflow: hidden;
}
.main-left{
  display: flex;
  flex-direction: column;
  max-width:600px;
  margin-top:60px;
  padding-right: 45px;
}
.main-right{
  height: 540px;
  width: 872px;
  position: relative;
  right: 0;
  background-image:url('https://res.cloudinary.com/swiggy/image/upload/fl_lossy,f_auto,q_auto,h_1340/Lunch1_vlksgq');
  background-size: cover;
  background-position: 100%;
  background-repeat: no-repeat;
}
.login-cont{
  display: flex;
  float: right;
}
.logo-main{
  width:180px;
  height: 50px;
}
.main-bg{
  height: 540px;
}
.login-button{
  display: inline-block;
  text-align: center;
  text-decoration: none;
  background-color: transparent;
  border: none;
  height:40px;
  line-height: 40px;
  font-size: 16px;
  color: black;
  width: 100px;
  font-weight: 700;
}
.signup-button{
  display: inline-block;
  text-align: center;
  text-decoration: none;
  background-color: black;
  color:white;
  border: none;
  height:40px;
  line-height: 40px;
  font-size: 16px;
  font-weight: 700;
  width: 100px;
}

.input-location{
  height: 60px;
  font-size: 18px;
  width: 75%;
  margin-top: 10px;
}
.input-location::placeholder{
  color: #282c3f;
  padding-left: 20px;
}
.dark{
  text-decoration: none;
  color: #686b78;
  font-weight: 600;
}
.light{
  text-decoration: none;
  color: #93959f;
  font-weight: 600;
}
.find-food{
  text-align: center;
  display: inline-block;
  font-weight: 700;
  font-size: 16px;
  text-decoration: none;
  color:white;
  background-color: #fc8019;
  width: 140px;
  height: 60px;
  line-height: 60px;
  margin-top: 10px;
}
.big-head{
  margin-top: 70px;
  font-size:40px;
}
.small-head{
  font-size:24px;
  color:#686b78;
}
.grey-head{
  margin-top: 35px;
  font-size: 15px;
  color: #a9abb2;
}

.svg-inline--fa{
  height: 1em;
  width: 1em;
}
.input-field{
  display: flex;
  flex-direction: row;
  position: relative;
  height: 60px;
}


.timer{
  padding-top: 30px;
  padding-bottom: 10px;
  padding-right: 0px;
}
/* Main section */


.features {
  background: #2b1e16;
  padding-bottom: 60px;

}

.features .container1 {
  display: flex;
  justify-content: space-between;
  flex-flow: row wrap;
    align-items: center;
}

.feature {
  text-align: center;
  padding-top: 80px;
}

@media (min-width: 900px) {
  .features .container1 {
    justify-content: space-around;
    
  }

  .feature {
    padding-top: 0px;
  }
}

.feature img {
  height: 200px;
  margin-bottom: 40px;
}

.feature h2 {
  font-size: 20px;
  font-weight: 600;
  margin-bottom: 20px;
  color: white;
}

.feature p {
  font-size: 15px;
  width: 260px;
  color: #e0cbc1;
}



/* Restaurant .js */

*{
  margin: 0;padding: 0;box-sizing: border-box;
}
.body_container{
  display: flex;
  justify-content: space-around;
  align-items: center;
  color:#000; 
  flex-wrap: wrap;  
  margin-bottom: -5px;
}
.body_container .screenshot{
  width: 55vw;
 
}
.body_container .left_child{
  width:40vw;
  min-width: 300px;
  max-width: 550px;
  color:#000; 
 padding: 5px;
}
.body_container div h1{
  width: 250px;
  margin-bottom: 30px;
}
.body_container .screenshot img{
  width: 35vw;
  max-width: 300px;
  
}
/* .stores{
  display: inline;
  justify-content: space-around;
  padding-right: 20px;
} */
.body_container .left_child .stores img{
  width: 150px;
  margin-top: 30px;
  padding-right: 20px;
}
@media (max-width:750px) {
  .body_container .screenshot{
      display: inline;
      
      
  }
 
  .body_container .left_child{
      width: 60vw;
  }
}


/* footer */

  footer {
    background: #000;
  }
  
  footer .container3 {
    display: flex;
    flex-flow: row wrap;
    justify-content: space-around;
    align-items: center;
  }
  
  footer section {
    padding-top: 60px;
  }
  
  footer h3 {
    color: gray;
    font-size: 15px;
    text-transform: uppercase;
    font-weight: 600;
    margin-bottom: 20px;
  }
  
  footer a {
    color: white;
    text-decoration: none;
    font-size: 17px;
    line-height: 2;
  }
  
  footer a:hover {
    font-weight: bold;
  }
  
  footer a img:hover {
    transform: scale(1.1);
  }
  
  footer nav {
    min-width: 18ch;
  }

  /* socilas */
  ._3zqGM {
    
    display: flex;
    flex-grow: 1;
    align-items: center;
    padding: 29px 0;
}

@media (max-width: 600px)
._26WmF {
    margin-top: 20px;
}
._26WmF {
    fill: #fff;
    width: 142px;
}

@media (max-width: 600px)
._2BWW_ {
    margin-top: 20px;
}
._2BWW_ {
    
    flex-grow: 1;
    text-align: center;
    font-size: 20px;
    letter-spacing: -.6px;
}

@media (max-width: 600px)
._2-f1I {
    margin-top: 20px;
}

._1Az3W {
    margin-left: 30px;
    display: inline-block;
   
    transform: scaleX(1);
    will-change: transform;
    transition: transform .5s cubic-bezier(.215,.61,.355,1);
}

/* general utility styles */
.container4 {
  max-width: 1140px;
  margin: auto;
  padding: 0 16px;
}
  
